<template>
  <div>
    <h1>我是第二个组件</h1>
    <p>我想输出点数据：<span class="dataStyle">{{ dataCon }}</span></p>
  </div>
</template>

<script>
export default {
  // name: 'firstVue',
  data () {
    return {
      dataCon: '我不是动态数据'
    }
  }
}
</script>

<style>
  .dataStyle {
    color: deepskyblue;
  }
</style>



