<template>
  <div>
    <div class="orderinfo clear_f">
      <div class="top" id="from-city">天津市市</div>
      <div class="info-arrow float_l">&nbsp;</div>
      <!-- <div style="float:left;margin-top:2px;" id="to-city">{{ GPSList.to }}</div> -->
      <div class="top" id="to-city">北京市市</div>
      <div style="clear: both"></div>
      <!-- <div style="float:left;margin-top:2px">预计到达时间：{{ GPSList.date }} 00:00</div> -->
      <div class="top">预计到达时间：2012-12-22 00:00</div>
      <!-- <div style="float:left;margin-top:2px" id="status-info">{{ GPSList.status }}</div> -->
      <div class="top" id="status-info">签收</div>
      <div class="top">订单号：9057153385</div>
      <div class="top">物流单号：ORSH19011600038</div>
      <div class="clear"></div>
    </div>
    <div>
      <ul>
        <li v-for="(item,i) in arrButton" :key="item">
          <a @click="buttonClick(i)" :class="{'active': numA === i}" href="javascript:;">{{ item }}</a>
        </li>
      </ul>
    </div>
    <div style="background-color: #fff;">
      <!-- <component :is="currentTabComponent"></component> -->
    <time-line v-show="numA === 0"></time-line>
      <!-- <second-vue v-show="numA !== 0"></second-vue> -->
      <second-vue v-show="numA === 1"></second-vue>
      
    </div>
  </div>
</template>

<script>
  // import timeLine from '@/components/lightTimeline.vue'
  import timeLine from '@/components/timeLine.vue'
  import secondVue from '@/components/second.vue'
  export default {
    // name: 'trace',
    data() {
      return {
        arrButton: ['在途模式', '地图模式'],
        // arrButton: ['在途模式', '地图模式', '在途模式2'],
        numA: 0,
        currentTabComponent: 'timeLine'
      }
    },
    components: {
      timeLine,
      secondVue
    },
    created() {
      
    },
    methods: {
      buttonClick(i) {
        this.numA = i
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
  .orderinfo {
    margin: auto;
    max-width: 800px;
    background-color: #fff;
    padding: 8px 10px;
    color: #666;
    font-size: 14px;
    font-weight: bold;
  }

  .orderinfo .info-arrow {
    background: url(../assets/images/arrow.png) no-repeat right;
    width: 38px;
  }

  .orderinfo>div {
    margin-right: 15px;
    line-height: 30px;
    color: #666;
    font-size: 14px;
  }

  .orderinfo>div.top {
    float: left;
    margin-top: 2px;
  }

  ul {
    width: 100%;
    display: flex;
    display: -webkit-flex;
  }

  ul>li {
    width: 50%;
  }

  ul>li>a {
    padding: .5rem 1rem;
    display: block;
    text-align: center;
  }

  ul>li>a.active {
    background-color: #e91e63;
    color: #fff;
  }

  ul>li {
    >a {
      border: 1px solid rgba(0, 0, 0, 0.125);
      -webkit-tap-highlight-color: transparent;
    }
  }
</style>



